/* pc */
a[href^="tel:"] {
  pointer-events: auto;
  @include mq("md") {
    pointer-events: none;
  }
}

/*  */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
  @include mq("md") {
    &:hover {
      opacity: 0.8;
    }
  }
}
