@use "sass:math";

////////////////////
// 
////////////////////
// 
$inner: 1100px;
// 

// 
$padding-pc: 25px;
$padding-sp: 20px;
// PCPC25px


////////////////////
// 
////////////////////

$base-font-family: "Gothic A1", sans-serif;
$second-font-family: "League Spartan", sans-serif;
$third-font-family: "Lato", sans-serif;
// 

$fz-base: 1rem;
$lh-base: 1.5;

// 

$light: 300;
$regular: 400;
$medium: 500;
$semibold: 600;
$bold: 700;
$black1: 900;

////////////////////
// 
////////////////////

//
$black: #000000;
$blue1: #5B83DB;
$blue2: #3068E3;
$blue3: #D0DBEF;
$blue4: #DFE8F7;
$grey: #CDCDCD;
$yellow1: #ECE025;
$yellow2: #F6E900;
$yellow3: #FFFCD9;
$white: #fff;
$red: #E20000;

body {
  font-size: $fz-base;
  line-height: $lh-base;
  font-family: $base-font-family;
  color: $black;
}

////////////////////
// 
////////////////////

//
$breakpoints: (
  sm: 374,
  md2: 540,
  md: 768,
  lg: 1024,
  xl: 1440,
);



//
$mediaquerys: (
  sm: "screen and (min-width: #{map-get($breakpoints,'sm')}px)",
  md2: "screen and (min-width: #{map-get($breakpoints,'md2')}px)",
  md: "screen and (min-width: #{map-get($breakpoints,'md')}px)",
  lg: "screen and (min-width: #{map-get($breakpoints,'lg')}px)",
  xl: "screen and (min-width: #{map-get($breakpoints,'xl')}px)",
);

// @include mq(){}md
@mixin mq($mediaquery: md) {
  @media #{map-get($mediaquerys, $mediaquery)} {
    @content;
  }
}

@mixin mq2($mediaquery: md2) {
  @media #{map-get($mediaquerys, $mediaquery)} {
    @content;
  }
}

@mixin mq1($mediaquery: sm) {
  @media #{map-get($mediaquerys, $mediaquery)} {
    @content;
  }
}

// rem16px10px1610
@function rem($pixels) {
  @return math.div($pixels, 16) * 1rem;
}

////////////////////
// 
////////////////////

//768px
.u-desktop {
  display: none;

  @include mq("md") {
    display: block;
  }
}

//
.u-mobile {
  @include mq("md") {
    display: none;
  }
}

